.mainContainer {
    /* padding: 0!important;
    margin: 0!important; */
    width: 100%;
}

.courseTitle {
  color: #031926;
  padding-left: 35px!important;
}
.courseTitleSub {
  color: #77aca2;
  font-size: 70%;
}

.courseIcons {
  font-size: 15px;
  height: 40px !important;
  width: 40px !important;
  border-radius: 8px;
  background-color: #77aca2;
  color: #031926;
}

.courseIcons:hover {
  background-color: #031926;
  color: #77aca2;
}

.firstRowLeft {
    text-align: left;
}
.firstRowRight {
    text-align: right;
}

.secondRow {
    margin-top: 70px;
    margin-left: 1px!important;
    background-color: #77ACA2;
    width: 100%;
    padding: 15px;
}
.courseNavigationIcons {
    color: #031926;
}

.courseNavigationLeft {
    font-size: 18px;
    text-align: left;
    color: #031926;
}
.courseNavigationCenter {
    font-size: 18px;
    text-align: center;
    color: #031926;
    margin-top: 7px;
}
.courseNavigationRight {
    font-size: 18px;
    text-align: right;
    color: #031926;
}

.navTopic {
    background-color: white;
    box-shadow: 5px 10px 20px #888888;
    border-top-right-radius: 6px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}

.paddingTop {
    padding-top: 30px;
    padding-left: 30px;
}

.quizQuestion {
    background-color: #EEEEEE;
    color: #031926;
    padding: 20px;
    border: 1px solid #031926;
}

.quizOption {
    padding: 20px;
    border: 2px solid black;
    width: 80%;
}

.quizRadio {
    width: 20px;
    height: 20px;
}

@media (max-width: 575.98px){
    .courseNavigationLeft {
        text-align: center!important;
    }
    .courseNavigationCenter {
        text-align: center!important;
    }
    .courseNavigationRight {
        text-align: center!important;
    }
    .courseTitle {
        font-size: 16px;
        padding-left: 0px!important;
    }
    .courseTitleSub {
        font-size: 16px;
    }
    .firstRowLeft {
        text-align: center;
    }
    .firstRowRight {
        text-align: center;
    }
    .paddingTop {
        padding-left: 0px;
    }
    .quizOption {
        padding: 10px;
        border: 2px solid black;
        width: 60%;
        font-size: 10px;
    }
}