.background {
  background-color: #468189;
  color: #E9ECEF;
  padding: 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 80%;
  margin: 0 auto;
}

.breadcrumb {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  width: 80%;
  margin: 0 auto;
}

.topicContainer {
  background-color: #468189;
  color: black;
  font-weight: bold;
  padding: 20px !important;
  border-radius: 10px;
}

@media (max-width: 575.98px) {
  .display {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .display {
    display: none;
  }
}

.topicRight {
  color: #468189;
}
.topicLink {
  color: #468189;
}

.topicLink:hover {
  color: black;
}

.courseTopicContainer {
  border-top: 4px solid #031926;
  background-color: #468189;
  width: 100%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.courseTopicText {
  color: #E9ECEF;
  text-align: center;
  padding-top: 10px;
  /* padding-bottom: 10px; */
}

.accordionCourse {
  margin: 0 auto;
  padding: 10px;
}

.cardBody {
  background-color: #E9ECEF;
}

.horizontalLine {
  background-color: #E9ECEF;
}

.videoContainer {
  border-top: 4px solid #031926;
  border-radius: 8px;
  background-color: #468189;
  width: 100%;
  padding: 20px 40px;
}

.videoContainerText {
  color: #E9ECEF;
}

#nav-video-tab {
  background-color: #031926;
  color: #E9ECEF;
  border-color: #031926;
  border-bottom: 0px;
}

.btnAccordian {
  text-align: left !important;
  background: #031926!important;
}

.btnAccordianText {
  color: #E9ECEF!important;
}

.tableBackgroundColor {
  background-color: #468189!important;
}

.courseJumbotron {
  margin-top: -35px;
  border-top: 4px solid #031926;
  border-top-left-radius: 0px!important;
  border-top-right-radius: 0px!important;
}

.navBorder {
  border-bottom: 0px solid #468189!important;
}

.overflow{
  overflow-y: scroll ;
  overflow-x: hidden;
  height: 600px;
}

.buttonText {
  font-size: 12px!important;
  color: #E9ECEF!important;
}

.thColor {
  color: #E9ECEF!important;
}

.mainCardClass {
  border: 0px!important;
  border-bottom: 1px solid #E9ECEF!important;
}