.profileContainer {
  background-color: white;
  box-shadow: 5px 10px 20px #888888;
  border-radius: 6px;
  padding: 30px;
}

.nameRow {
  margin-top: 30px;
}
.formName {
  width: 100% !important;
}

.imp {
  color: red;
}
.labels {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: -2px;
}

.customFile {
  width: 82% !important;
}
.customLabel {
  text-align: left;
}

.notice {
    font-size: 8px;
    font-weight: normal;
}

.radioButton {
    width: 15px!important;
    height: 15px!important;
}

.cnicstatus {
    margin-top: 20px;
}

.cnic {
    margin-left: 25px;
}

.textSize {
  font-size: 11px;
}

@media (max-width: 575.98px) {
  .formName {
    margin-bottom: 10px;
  }
  .nameRow {
    margin-top: 20px;
  }
  .cnicstatus {
    margin-top: 0px;
}
.cnic {
    margin-left: 0px;
}
}
